<script setup>
import { getCEMSDeviceData } from "@/api/CEMS";
import { ref, reactive, onMounted } from "vue";

const formInline = reactive({
    timeSize: 'DataRealTimeHisColl',
    data: '平均值',
    deviceName: "130424LRTTS001",
    total: ""
})

//let定义后续使用加.value
let currPage1 = ref(1)
let pageSize1 = ref(10)

function search() {
    let payload = {
        currPage: currPage1.value,
        pageSize: pageSize1.value,
        collName: formInline.timeSize,//时间粒度
        deviceCode: formInline.deviceName,

    }
    getVocData(payload);


}

let vocDate = reactive({
    vocDate1: [],
})
let dataGet = null
function getVocData(payload) {
    getCEMSDeviceData(payload).then((res) => {
        vocDate.vocDate1 = []
        console.log(res.data.data.dataSet);
        dataGet = res.data.data.dataSet
        formInline.total = res.data.data.total
        if (formInline.timeSize != 'DataRealTimeHisColl') {
            for (let x = 0; x < dataGet.length; x++) {
                let linshi = {}
                linshi["dataTime"] = dataGet[x].dataTime
                linshi["1"] = dataGet[x].data[formInline.data]['二氧化硫']
                linshi["2"] = dataGet[x].data[formInline.data]['二氧化硫Zs']
                linshi["3"] = dataGet[x].data[formInline.data]['一氧化氮']
                linshi["4"] = dataGet[x].data[formInline.data]['一氧化氮Zs']
                linshi["5"] = dataGet[x].data[formInline.data]['烟尘']
                linshi["6"] = dataGet[x].data[formInline.data]['烟尘Zs']
                linshi["7"] = dataGet[x].data[formInline.data]['氮氧化物']
                linshi["8"] = dataGet[x].data[formInline.data]['氮氧化物Zs']
                linshi["9"] = dataGet[x].data[formInline.data]['氧气含量']
                linshi["10"] = dataGet[x].data[formInline.data]['烟气温度']
                linshi["11"] = dataGet[x].data[formInline.data]['烟气压力']
                linshi["12"] = dataGet[x].data[formInline.data]['烟气流速']
                linshi["13"] = dataGet[x].data[formInline.data]['废气']
                linshi["14"] = dataGet[x].data[formInline.data]['烟气湿度']
                linshi["15"] = dataGet[x].data[formInline.data]['二氧化硫-PJZS']
                linshi["16"] = dataGet[x].data[formInline.data]['烟尘-PJZS']
                linshi["17"] = dataGet[x].data[formInline.data]['氮氧化物-PJZS']

                if (formInline.deviceName == "130424LRTTS001") {

                    linshi["name"] = "焙烧CEMS"
                    console.log(linshi);
                } else if (formInline.deviceName == "130424LRTTS002") {
                    linshi["name"] = "隧道窑CEMS"
                    console.log(linshi);
                }
                else if (formInline.deviceName == "130424RTTS0005") {
                    linshi["name"] = "cems3"
                    console.log(linshi);
                }
                else {
                    linshi["name"] = "石墨化CEMS"
                    console.log(linshi);
                }
                vocDate.vocDate1.push(linshi)
            }
        } else {
            for (let x = 0; x < dataGet.length; x++) {
                let linshi = {}
                linshi["dataTime"] = dataGet[x].dataTime
                linshi["1"] = dataGet[x].data['二氧化硫']
                linshi["2"] = dataGet[x].data['二氧化硫Zs']
                linshi["3"] = dataGet[x].data['一氧化氮']
                linshi["4"] = dataGet[x].data['一氧化氮Zs']
                linshi["5"] = dataGet[x].data['烟尘']
                linshi["6"] = dataGet[x].data['烟尘Zs']
                linshi["7"] = dataGet[x].data['氮氧化物']
                linshi["8"] = dataGet[x].data['氮氧化物Zs']
                linshi["9"] = dataGet[x].data['氧气含量']
                linshi["10"] = dataGet[x].data['烟气温度']
                linshi["11"] = dataGet[x].data['烟气压力']
                linshi["12"] = dataGet[x].data['烟气流速']
                linshi["13"] = dataGet[x].data['废气']
                linshi["14"] = dataGet[x].data['烟气湿度']
                linshi["15"] = dataGet[x].data['二氧化硫-PJZS']
                linshi["16"] = dataGet[x].data['烟尘-PJZS']
                linshi["17"] = dataGet[x].data['氮氧化物-PJZS']

                if (formInline.deviceName == "130424LRTTS001") {

                    linshi["name"] = "焙烧CEMS"
                    console.log(linshi);
                } else if (formInline.deviceName == "130424LRTTS002") {
                    linshi["name"] = "隧道窑CEMS"
                    console.log(linshi);
                }
                else if (formInline.deviceName == "130424RTTS0005") {
                    linshi["name"] = "cems3"
                    console.log(linshi);
                }
                else {
                    linshi["name"] = "石墨化CEMS"
                    console.log(linshi);
                }

                vocDate.vocDate1.push(linshi)
            }
        }
    });
}

//进入显示
onMounted(() => {
    search()
})     
</script>
  
